.skills {
  padding: 1em;
  margin: 1em 0;
  background: linear-gradient(to right, #f5f5f5, #e0e0e0);
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

  .skill {
    //display: flex;
    //align-items: center;
    //justify-content: space-between;
    //padding: 5px;
    //margin: 0 0 10px;
    //gap: 0;

    .skill-roll {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      border-radius: 5px;
      background-color: #f5f5f5;
      border: 1px solid #ccc;
      margin-right: 10px;

      label {
        margin-left: 5px;
        font-size: small;
      }
    }

    .skill-label {
      flex: 1;
      font-weight: bold;
      cursor: pointer;
      transition: color 0.3s;
      text-align: left;

      &.highlight-link {
        text-decoration: none;
      }

      &.hover-green:hover {
        color: green;
      }
    }

    .skill-rank {
      display: flex;
      align-items: center;
      justify-content: flex-end;

      i {
        margin-left: 5px;
        cursor: pointer;
        transition: color 0.3s;
        font-size: 1.2em;

        &.hover-action:hover {
          color: red;
        }
      }
    }
  }
}
