  position: relative;
  overflow: hidden; /* Ensures nothing spills outside the container */
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 8%;
}
.pl1e .temperature-bar-gradient {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background: linear-gradient(to right, #ffffff 0%, #add8e6 11%, #0000ff 22%, #00ffff 33%, #00ff00 44%, #ffff00 55%, #ffd700 66%, #ff8c00 77%, #ff0000 88%, #8b0000 100%); /* extreme hot: dark red */
  position: absolute;
  border: 1px solid #000000; /* Black border for visibility */
}
.pl1e .temperature-bar-cursor {
  width: 5px; /* Adjust cursor width as needed */
  height: 100%;
  background-color: #000; /* Cursor color */
  position: absolute;
  box-shadow: 0 0 3px 3px #ffffff; /* Add a white glow for dark backgrounds */
  border: 1px solid #000000; /* Add a black border for visibility against lighter backgrounds */
}
.pl1e .water-bar-container {
  background-color: #e0e0e0; /* Consistent with temperature bar */
  border-radius: 5px; /* Same rounded corners */
  height: 20px; /* Same height */
  position: relative;
  overflow: hidden; /* Ensures the fill doesn't spill outside */
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 8%;
}
.pl1e .water-bar-fill {
  width: 10%; /* Starts at 10% to represent the lowest water level */
  height: 100%; /* Fills the container height */
  background: linear-gradient(to right, #add8e6, #0000ff); /* Gradient from light blue to dark blue */
  border-radius: 5px; /* Rounded corners */
  position: absolute;
  top: 0;
  left: 0;
  transition: width 0.5s ease; /* Smooth transition for width changes */
