.hover-action {
  &:hover,
  &:focus {
    text-shadow: 0 0 10px red;
    cursor: pointer;
  }
}

.hover-green {
  &:hover,
  &:focus {
    text-shadow: 0 0 10px green;
  }
}

.highlight-green {
  text-shadow: 0 0 10px green;
}